//   HTTP-
export enum HTTPMethod {
    GET = 'get',
    POST = 'post'
}

//  
export type RequestParams<ReqT> = {
    method: HTTPMethod; //  , GET  POST
    endpoint: string; // API-endpoint,    
    headers: Record<string, string>; //    HTTP-

    /**
     *    .
     * -  GET-    query-    endpoint
     * -  POST-     JSON      ( )
     */
    data: ReqT;
}

//   
export enum StatusHTTP {
    OK = 200,
    NO_CONTENT = 204,
    BAD_REQUEST = 400,
    FORBIDDEN = 403,
    NOT_FOUND = 404,
    BAD_GATEWAY = 502
}

//  API
export type ApiResponse<SuccessT, ErrorT> =
    | {
    success: true;
    data: SuccessT;
    status: StatusHTTP;
}
    | {
    success: false;
    data: ErrorT;
    status: StatusHTTP;
}
    | {
    success: false;
    data: null;
    status: StatusHTTP;
};

//   ,        API
export interface IApiStore {
    //  url   . TODO:  url GitHub API   ApiStore
    readonly baseUrl: string;

    // ,     . TODO:    ApiStore
    request<SuccessT, ErrorT = any, ReqT = {}>(params: RequestParams<ReqT>): Promise<void>
}